#quote-box {
  width: 80%; /* Adjust width as needed */
  max-width: 600px; /* Adjust max-width as needed */
  margin: 0 auto; /* This centers the box horizontally */
  text-align: center; /* Optional: center text inside the box */
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: #f9f9f9;
}

#text {
  font-size: 24px;
  font-style: italic;
}

#author {
  font-size: 20px;
  margin-top: 10px;
}

#new-quote {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
}

#tweet-quote {
  display: block;
  margin-top: 10px;
  color: #1da1f2;
  text-decoration: none;
}

#tweet-quote:hover {
  text-decoration: underline;
}
